<template>
  <div id="ya">
    <div class="head">
      <ul class="bar">
        <a href="/resume" title="返回首页">
          <li>さくら * ya</li>
        </a>
        <router-link to="/music">
          <li :class="{c:s===1}" @click="s1()">blues口琴谱</li>
        </router-link>
        <router-link to="/design">
          <li :class="{c:s===2}" @click="s2()">photoshop图片</li>
        </router-link>
        <router-link to="/japanese">
          <li :class="{c:s===4}" @click="s4()">学习Japanese</li>
        </router-link>
        <router-link to="/english">
          <li :class="{c:s===5}" @click="s5()">学习English</li>
        </router-link>
        <li :class="{c:s===3}" @click="s3()" id="note">
          随笔
          <div class="timelineBox">
            <router-link to="/timeline">
              <div class="timeline ho">时间轴</div>
            </router-link>
            <router-link to="/note">
              <div class="article ho">文章集</div>
            </router-link>
          </div>
        </li>
      </ul>
    </div>
    <div class="y-container">
      <router-view/>
    </div>
  </div>
</template>



<script>
export default {
  name: "ya",
  data() {
    return {
      s: 1
    };
  },
  methods: {
    s1: function() {
      this.s = 1;
    },
    s2: function() {
      this.s = 2;
    },
    s3: function() {
      this.s = 3;
    },
    s4: function() {
      this.s = 4;
    },
    s5: function() {
      this.s = 5;
    }
  },
  created() {
    //loading
    document.onreadystatechange = function() {
      //interactive
      if (this.readyState === "complete") {
        //design页面的图片预加载
        var items = [
          { src: "../../static/y1.png", time: "2017-4-4" },

          { src: "../../static/design/ji.png", time: "2019-1-31" },
          { src: "../../static/design/d1.gif", time: "2018-4-4" },
          { src: "../../static/design/d3.png", time: "2018-4-4" },
          { src: "../../static/design/san.png", time: "2019-4-10" },

          { src: "../../static/design/baby2.png", time: "2019-1-31" },

          { src: "../../static/design/zz.png", time: "2019-1-31" },
          { src: "../../static/design/d2.gif", time: "2018-4-4" },

          { src: "../../static/design/三只2.png", time: "2019-1-31" }
        ];
        for (var i = 0; i < items.length; i++) {
          var img = new Image();
          img.src = items[i].src;
        }
        document.querySelector(".loading").style.display = "none";
      }
    };
  },
  mounted() {}
};
</script>

<style scoped>
#ya {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  /*margin-top: 60px;*/
  position: absolute;
  z-index: 1;
  width: 100%;
  min-width: 1024px;
  height: 100%;
  /*background-image:url("../static/yinfu.jpg");
  background-repeat:repeat;
  backgroud-position:center;*/
}
a {
  text-decoration: none;
  color: #d3d3d3;
}
.head {
  width: 100%;
  height: 60px;
  line-height: 60px;
  background-color: #232a34;
  color: #fff;
  position: fixed;
  z-index: 4;
}
ul {
  position: relative;
  list-style: none;
}
.bar {
  padding-left: 12.5%;
}
li {
  float: left;
  margin-right: 20px;
}
li:hover {
  color: #e8785e;
}
.y-container {
  width: 70%;
  height: 100%;
  min-width: 1024px;
  margin-top: 100px;
  position: absolute;
  left: 15%;
  /*border:1px solid #eee;*/
}
.c {
  /*color:#E75355;*/
  color: #e8785e;
}
#note {
  position: relative;
  color: #d3d3d3;
}
#note:hover .timelineBox {
  display: block;
}
.timelineBox {
  position: absolute;
  top: 50px;
  display: none;
  background-color: #232a34;
  color: #d3d3d3;
  box-shadow: 0px 0px 15px #d3d3d3;
  margin-left: -15px;
  border-radius: 5px;
  padding: 10px 0;
}
.timelineBox:before {
  display: block;
  content: "";
  border-width: 8px 8px 8px 8px;
  border-style: solid;
  border-color: transparent transparent #232a34 transparent;
  position: absolute;
  left: 30px;
  top: -16px;
}
.timelineBox .timeline,
.timelineBox .article {
  width: 60px;
  padding: 0px 10px;
  height: 30px;
  line-height: 30px;
  text-align: center;
}

.ho:hover {
  color: #e8785e;
}
</style>



